import { useTheme, styled } from '@material-ui/core/styles'
import { Box, IconButton } from '@material-ui/core'

const RootStyle = styled(Box)(({ theme }) => ({
  top: 0,
  bottom: 0,
  zIndex: 9,
  width: '100%',
  margin: 'auto',
  display: 'flex',
  position: 'absolute',
  alignItems: 'center',
  justifyContent: 'space-between',
}))

const ArrowLeftStyle = styled(Box)(({ theme }) => ({
  backgroundImage: 'url(/images/arrow-left-bg.png)',
  backgroundSize: '100% 100%',
  opacity: 1,
  height: 600,
  width: 124,
  display: 'flex',
  cursor: 'pointer',
  alignItems: 'center',
  justifyContent: 'center',
  transition: theme.transitions.create('opacity'),
  // '&:hover': {
  //   opacity: 0.5,
  // },
}))

const ArrowRightStyle = styled(Box)(({ theme }) => ({
  backgroundImage: 'url(/images/arrow-right-bg.png)',
  backgroundSize: '100% 100%',
  opacity: 1,
  height: 600,
  width: 124,
  display: 'flex',
  cursor: 'pointer',
  alignItems: 'center',
  justifyContent: 'center',
  transition: theme.transitions.create('opacity'),
  // '&:hover': {
  //   opacity: 0.5,
  // },
}))


export default function CarouselControlsArrowsBasic2({
  arrowLine,
  onNext,
  onPrevious,
  ...other
}) {

  return (
    <RootStyle {...other}>
      <ArrowLeftStyle size="small" onClick={onPrevious}>
        <Box component="img" src="/images/arrow-left.png" />
      </ArrowLeftStyle>

      <ArrowRightStyle size="small" onClick={onNext}>
        <Box component="img" src="/images/arrow-right.png" />
      </ArrowRightStyle>
    </RootStyle>
  )
}
