export default function Buttons(theme) {
  return {
    MuiButton: {
      variants: [
        {
          props: { variant: 'big' },
          style: {
            height: '70px',
            backgroundColor: '#AB882E',
            color: 'fff',
            // width: {xl: 626, lg: 600, md: 400}
          },
        },
      ],
      styleOverrides: {
        root: {
        }
      }
    },
    MuiTooltip: {
      styleOverrides: {
        tooltip: {
          backgroundColor: '#AB882E',
          borderRadius: '20px'
        }
      }
    },
  };
}
