import { NavLink as RouterLink, useLocation } from 'react-router-dom'
// material
import { styled } from '@material-ui/core/styles'
import {
  Box,
  Button,
  AppBar,
  Toolbar,
  Container,
  Stack,
} from '@material-ui/core'
// components
import Logo from '../../components/Logo'
import SocialLinks from 'components/SocialLinks'
import { MHidden } from '../../components/@material-extend'
//
import MenuDesktop from './MenuDesktop'
import MenuMobile from './MenuMobile'
import navConfig from './MenuConfig'

// ----------------------------------------------------------------------

const APP_BAR_MOBILE = 64
const APP_BAR_DESKTOP = 130

const ToolbarStyle = styled(Toolbar)(({ theme }) => ({
  height: APP_BAR_MOBILE,
  // paddingLeft: 64,
  paddingLeft: 15,
  paddingRight: 36,
  // paddingTop: 8,
  paddingTop: 15,
  alignItems: 'flex-start',
  background: 'url(/images/mobile-navbar-bg.png) no-repeat',
  backgroundSize: '100% 180%',
  transition: theme.transitions.create(['height', 'background-color'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter,
  }),
  [theme.breakpoints.up('md')]: {
    height: APP_BAR_DESKTOP,
    background: 'url(/images/navbar-bg.png) no-repeat',
    backgroundSize: '100% 100%',
  },
}))

export default function MainNavbar() {
  const { pathname } = useLocation()
  const isHome = pathname === '/'

  return (
    <AppBar sx={{ boxShadow: 0, bgcolor: 'transparent' }}>
      <ToolbarStyle disableGutters>
        <Stack
          direction="row"
          alignItems="center"
          justifyContent="space-between"
          sx={{ flexGrow: 1 }}
        >
          <RouterLink to="/">
            <Stack direction="row" alignItems="center">
              <Logo />
              <Box
                component="img"
                src="/images/HunterDoge.png"
                sx={{ width: 0.45, height: 0.45, ml: 2.5}}
              />
            </Stack>
          </RouterLink>
          <Box sx={{ flexGrow: 1 }} />

          <MHidden width="mdDown">
            <MenuDesktop isHome={isHome} navConfig={navConfig} />
          </MHidden>

          <MHidden width="mdDown">
            <a href='https://t.me/hunterdogeofficial' target='_blank' style={{ textDecoration: 'none'}} rel="noreferrer">
            <Button
              variant="contained"
              target="_blank"
              sx={{
                ml: {
                  xl: 10,
                  lg: 2,
                },
                fontWeight: 500,
                borderRadius: '25px',
                border: '2px solid #B78300',
                background: 'white',
                color: 'primary.main',
              }}
            >
              JOIN COMMUNITY
            </Button>
            </a>
          </MHidden>

          <MHidden width="mdUp">
            <MenuMobile isHome={isHome} navConfig={navConfig} />
          </MHidden>
        </Stack>
      </ToolbarStyle>
      <MHidden width="mdDown">
        <Stack direction="row" justifyContent="flex-end" sx={{ mt: -5 }}>
          <Box sx={{ transform: 'scale(0.9)', mr: 2 }}>
            <SocialLinks />
          </Box>
        </Stack>
      </MHidden>
    </AppBar>
  )
}
