import PropTypes from 'prop-types'
import { useState, useEffect } from 'react'
import { NavLink as RouterLink, useLocation } from 'react-router-dom'
// material
import { alpha, styled } from '@material-ui/core/styles'
import {
  Box,
  List,
  Drawer,
  Link,
  Collapse,
  ListItemText,
  ListItemIcon,
  ListItemButton,
  Button,
  IconButton
} from '@material-ui/core'
// components
import Logo from '../../components/Logo'
import SvgIconStyle from 'components/SvgIconStyle'
import NavSection from '../../components/NavSection'
import Scrollbar from '../../components/Scrollbar'
import SocialLinks from 'components/SocialLinks'
import { MIconButton } from '../../components/@material-extend'
//
import menuConfig from './MenuConfig'

// ----------------------------------------------------------------------

const ICON_SIZE = 22
const ITEM_SIZE = 48
const PADDING = 2.5

const ListItemStyle = styled(ListItemButton)(({ theme }) => ({
  ...theme.typography.body2,
  height: ITEM_SIZE,
  marginTop: 40,
  flexDirection: 'column',
  justifyContent: 'center',
  gap: 10,
  textTransform: 'capitalize',
  // paddingLeft: theme.spacing(PADDING),
  // paddingRight: theme.spacing(2.5),
  color: theme.palette.text.secondary,
}))

function MenuMobileItem({ item, isOpen, isActive, onOpen, onClick }) {
  const { title, path, icon, children } = item

  return (
    <a href={`${path}`} style={{ textDecoration: 'none' }}>
    <ListItemStyle
     
      onClick={onClick}
      sx={{
        ...(isActive && {
          color: 'primary.main',
          fontWeight: 'fontWeightMedium',
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.selectedOpacity,
            ),
        }),
      }}
    >
      
      <ListItemIcon sx={{mr: 0}}>
        <Box component="img" src="/images/paw.png" />
      </ListItemIcon>
      <ListItemText disableTypography primary={title} />
    </ListItemStyle>
    </a>
  )
}

MenuMobile.propTypes = {
  isOffset: PropTypes.bool,
  isHome: PropTypes.bool,
}

export default function MenuMobile({ isOffset, isHome }) {
  const { pathname } = useLocation()
  const [open, setOpen] = useState(false)
  const [mobileOpen, setMobileOpen] = useState(false)

  useEffect(() => {
    if (mobileOpen) {
      handleDrawerClose()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname])

  const handleDrawerOpen = () => {
    setMobileOpen(true)
  }

  const handleDrawerClose = () => {
    setMobileOpen(false)
  }

  const handleOpen = () => {
    setOpen(!open)
  }

  return (
    <>
      <MIconButton
        onClick={handleDrawerOpen}
        sx={{
          ml: 1,
          mt: -3,
          ...(isHome && { color: 'common.white' }),
          ...(isOffset && { color: 'text.primary' }),
        }}
      >
        <Box component="img" src="/images/hamburger.png" />
      </MIconButton>

      <Drawer
        open={mobileOpen}
        anchor="top"
        onClose={handleDrawerClose}
        ModalProps={{ keepMounted: true }}
        PaperProps={{
          sx: {
            pb: 5,
            display: 'flex',
            alignItems: 'center',
            backgroundColor: '#FFDA00',
            position: 'relative',
          },
        }}
      >
        <IconButton onClick={handleDrawerClose} sx={{position: 'absolute', right: '10px', top: '10px'}}>
          <Box component="img" src="/images/ham-close.png" />
        </IconButton>
        <Link component={RouterLink} to="/" sx={{ display: 'inline-flex' }}>
          <Logo sx={{ mx: PADDING, my: 3 }} />
        </Link>
        <Box component="img" src="/images/HunterDoge.png" sx={{ width: 0.3 }} />

        <List disablePadding>
          {menuConfig.map((link) => (
            <MenuMobileItem
              onClick={handleDrawerClose}
              key={link.title}
              item={link}
              isOpen={open}
              onOpen={handleOpen}
              isActive={pathname === link.path}
            />
          ))}
        </List>
        <a href='https://t.me/hunterdogeofficial' target='_blank' style={{ textDecoration: 'none'}} rel="noreferrer">
        <Button
          variant="contained"
          target="_blank"
          sx={{
            fontWeight: 500,
            borderRadius: '25px',
            border: '2px solid #B78300',
            background: 'white',
            color: 'primary.main',
            mt: 4,
          }}
        >
          JOIN COMMUNITY
        </Button>
        </a>
        <Box sx={{ transform: 'scale(0.8)', mt: 3 }}>
          <SocialLinks />
        </Box>
      </Drawer>
    </>
  )
}
