import { styled } from '@material-ui/core/styles'
import { Box } from '@material-ui/core'
import {
  Hero,
  WhiteListing,
  AboutHunterDoge,
  HunterDogeCom,
  UseCases,
  Tokenomics,
  Parameters,
  TokenAllocation,
  InvestmentStages,
  Roadmap,
  Faq,
  Footer,
} from 'components/landing'

const RootStyle = styled(Box)({
  height: '100%',
  paddingTop: 40,
})

const ContentStyle = styled('div')(({ theme }) => ({
  overflow: 'hidden',
  position: 'relative',
  // background: 'url(/images/bg.png)',
  // backgroundSize: 'contain',
}))

const TopStyle = styled('div')(({ theme }) => ({
  marginTop: 50,
  background: 'url(/images/desktop-top-bg.png)',
  backgroundSize: '100% 96%',
  backgroundRepeat: 'no-repeat',
  [theme.breakpoints.down('md')]: {
    background: 'url(/images/mobile-top-bg.png)',
    backgroundSize: '100% 130%',
    marginTop: 10,
  },
}))

const BottomStyle = styled('div')(({ theme }) => ({
  // marginTop: 80,
  background: 'url(/images/desktop-bottom-bg.png)',
  backgroundSize: '100%',
  backgroundRepeat: 'no-repeat',
  [theme.breakpoints.down('md')]: {
    background: 'none',
  },
}))

const MobileBottomStyle = styled('div')(({ theme }) => ({
  // marginTop: 80,
  background: 'none',
  backgroundSize: '100% 100%',
  backgroundRepeat: 'no-repeat',
  [theme.breakpoints.down('md')]: {
    background: 'url(/images/mobile-bottom-bg.png)',
    backgroundSize: '100% 100%',
  },
}))
const FooterStyle = styled('div')(({ theme }) => ({
  // marginTop: 80,
  background: 'url(/images/footer-bg.png)',
  backgroundSize: '100%',
  backgroundRepeat: 'no-repeat',
  backgroundPosition: 'bottom',
  [theme.breakpoints.down('md')]: {
    background: 'url(/images/footer-mobile-bg.png)',
    backgroundSize: '100%',
    backgroundRepeat: 'no-repeat',
  backgroundPosition: 'bottom',
  },
}))
export default function LandingPage() {
  return (
    <RootStyle id="move_top">
      <ContentStyle>
        <TopStyle>
          <Hero />
          <WhiteListing />
          <AboutHunterDoge />
        </TopStyle>
        <HunterDogeCom />
        <UseCases />
        <Tokenomics />
        <BottomStyle>
          <Parameters />
          <MobileBottomStyle>
            <TokenAllocation />
            <InvestmentStages />
            <Roadmap />
            <FooterStyle>
              <Faq />
              {/* <Footer /> */}
            </FooterStyle>
          </MobileBottomStyle>
        </BottomStyle>
      </ContentStyle>
    </RootStyle>
  )
}
