import { Box } from '@material-ui/core'

export default function CarouselAnimation() {
  return (
    <Box sx={{ position: 'relative' }}>
      <Box component="img" src="/images/carousel2-bg.png" />
      <Box
        component="img"
        src="/gifs/detective_hunter.gif"
        style={{ position: 'absolute', top: '-30px', left: '8px' }}
      />
    </Box>
  )
}
