import { Box } from '@material-ui/core'

export default function CarouselAnimation() {
  return (
    <Box sx={{ position: 'relative' }}>
      <Box component="img" src="/images/carousel6-bg.png" />
      <Box
        component="img"
        src="/gifs/bitcoin_hunter.gif"
        style={{ position: 'absolute', top: -65, left: 20, transform: 'scale(0.8)' }}
      />
    </Box>
  )
}
