import { Link as ScrollLink } from 'react-scroll';
import { Link as RouterLink } from 'react-router-dom';
// material
import { styled } from '@material-ui/core/styles';
import { Grid, Link, Divider, Container, Typography, IconButton, Stack } from '@material-ui/core';
import Logo from '../../components/Logo';

// ----------------------------------------------------------------------

const SOCIALS = [
  { name: 'FaceBook', icon: '' },
  { name: 'Google', icon: '' },
  { name: 'Linkedin', icon: '' },
  { name: 'Twitter', icon: '' }
];

const LINKS = [
  {
    headline: 'Socials',
    children: [
      { name: 'Telegram', href: 'https://t.me/hunterdogeofficial' },
      { name: 'Instagram', href: 'https://www.instagram.com/hunterdogeofficial/' },
      { name: 'Twitter', href: 'https://twitter.com/hunterdoge_' },
      { name: 'Reddit', href: 'https://www.reddit.com/u/hunterdoge_/' },
      { name: 'Medium', href: 'https://medium.com/@hunterdoge' }
    ]
  },
  {
    headline: 'Contact',
    children: [
      { name: 'info@hunterdoge.com', href: 'mailto:info@hunterdoge.com' },
      { name: 'Coming Soon' }
    ]
  }
];

const RootStyle = styled('div')(({ theme }) => ({
  position: 'relative',
  background: 'transparent'
}));

// ----------------------------------------------------------------------

export default function MainFooter() {
  return (
    <RootStyle>
      <Divider />
      <Container maxWidth="lg" sx={{ pt: 10 }}>
        <Grid
          container
          justifyContent={{ xs: 'center', md: 'space-between' }}
          sx={{ textAlign: { xs: 'center', md: 'left' } }}
        >
          <Grid item xs={12} sx={{ mb: 3 }}>
            <ScrollLink to="move_top" spy smooth>
              <Logo sx={{ mx: { xs: 'auto', md: 'inherit' } }} />
            </ScrollLink>
          </Grid>
          <Grid item xs={8} md={3}>
            <Typography variant="body2" sx={{ pr: { md: 5 } }}>
              Thanks for reading till the end :)
            </Typography>

            <Stack
              spacing={1.5}
              direction="row"
              justifyContent={{ xs: 'center', md: 'flex-start' }}
              sx={{ mt: 5, mb: { xs: 5, md: 0 } }}
            >
              {SOCIALS.map((social) => (
                <IconButton key={social.name} color="primary" sx={{ p: 1 }}>
                  {/* <Icon icon={social.icon} width={16} height={16} /> */}
                </IconButton>
              ))}
            </Stack>
          </Grid>

          <Grid item xs={12} md={7}>
            <Stack spacing={5} direction={{ xs: 'column', md: 'row' }} justifyContent="space-between">
              {LINKS.map((list) => {
                const { headline, children } = list;
                return (
                  <Stack key={headline} spacing={2}>
                    <Typography component="p" variant="overline">
                      {headline}
                    </Typography>
                    {children.map((link) => (
                      <a href={link.href}style={{ textDecoration: 'none'}} >
                        <Link
                          // to={link.href}
                          key={link.name}
                          color="inherit"
                          variant="body2"
                          // component={RouterLink}
                          sx={{ display: 'block' }}
                        >
                          {link.name}
                        </Link>
                      </a>
                    ))}
                  </Stack>
                );
              })}
            </Stack>
          </Grid>
        </Grid>

        <Typography
          component="p"
          variant="body2"
          sx={{
            mt: 10,
            pb: 5,
            fontSize: 13,
            textAlign: { xs: 'center', md: 'left' }
          }}
        >
          © 2021. All rights reserved
        </Typography>
      </Container>
    </RootStyle>
  );
}
