import { Box } from '@material-ui/core'

export default function CarouselAnimation() {
  return (
    <Box sx={{ position: 'relative' }}>
      <Box component="img" src="/images/carousel1-bg.png" />
      <Box
        component="img"
        src="/gifs/reading_hunter.gif"
        style={{ position: 'absolute', top: 4, left: -14, transform: 'scale(0.9)' }}
      />
    </Box>
  )
}
