import { useTheme, styled } from '@material-ui/core/styles'
import { Box } from '@material-ui/core'

const RootStyle = styled(Box)(({ theme }) => ({
  zIndex: 9,
  display: 'flex',
  position: 'absolute',
  top: theme.spacing(2),
  right: theme.spacing(2),
}))

const ArrowStyle = styled(Box)(({ theme }) => ({
  padding: 6,
  backgroundImage: 'url(/images/arrow-right-bg)',
  opacity: 0.48,
  color: theme.palette.common.white,
  '&:hover': { opacity: 1 },
}))

export default function CarouselControlsArrowsBasic1({
  arrowLine,
  onNext,
  onPrevious,
  ...other
}) {
  return (
    <RootStyle {...other}>
      <ArrowStyle size="small" onClick={onPrevious}>
        <Box component="img" src="/images/arrow-left.png"/>
      </ArrowStyle>

      <ArrowStyle size="small" onClick={onNext}>
        <Box component="img" src="/images/arrow-right.png" />
      </ArrowStyle>
    </RootStyle>
  )
}
