const MOCK_CAROUSELS = [...Array(5)].map((_, index) => ({
  id: index,
  title: index,
  image: '/images/Home-Doge.png',
  description: index,
}))

const RootStyle = styled(Card)(({ theme }) => ({
  background: 'transparent',
  boxShadow: 'none',
  position: 'relative',
  width: '100%',
  '& .slick-list': {
    // boxShadow: theme.customShadows.z16,
    borderRadius: theme.shape.borderRadiusMd,
  },
}))

