import SvgIconStyle from 'components/SvgIconStyle'
import { Box, Stack, Link } from '@material-ui/core'
import { Link as RouterLink, Redirect } from 'react-router-dom'
const items = [
  { path: '#', icon: 'telegram' },
  // { path: '#', icon: 'twitter'},
  // { path: '#', icon: 'instagram'},
  // { path: '#', icon: 'reddit'},
  // { path: '#', icon: 'medium'}
]
const LinkItem = (path, icon) => {
  console.log(path, icon)
  return (
    <Link component={RouterLink} to={path}>
      <Stack
        justifyContent="center"
        alignItems="center"
        sx={{
          filter: 'drop-shadow(1px 3px 0px rgba(0, 0, 0, 0.1))',
          border: '5px solid #FFEDC0',
          width: 50,
            height: 50,
          backgroundColor: 'primary.main',
          borderRadius: '50%',
        }}
      >
        <SvgIconStyle color="paper" src={`/icons/${icon}.svg`} />
      </Stack>
    </Link>
  )
}

