import { Box } from '@material-ui/core'
import { motion } from 'framer-motion'

export default function Cloud({
  range = 50,
  duration = 4,
  sx,
  single = false,
  scale = 1
}) {
  return (
    <motion.div
      animate={{ x: [range, 0, range] }}
      transition={{ duration, repeat: Infinity }}
      style={{...sx}}
    >
      <Box
        component="img"
        src={single ? '/images/cloud-single.png' : '/images/cloud.png'}
        sx={{ transform: `scale(${scale})` , paddingTop: 6}}
      />
    </motion.div>
  )
}
